<template>
  <div class="page">
    <div id="circle"></div>
  </div>
</template>

<script>
import animationData from '../assets/json/demo' // 引入json文件

let that = null
export default {
  name: 'Loading',
  data () {
    return {
      progressValue: 0
    }
  },
  created () {
    that = this
    that.dynamicProgress()
    // lottie.loadAnimation({
    //   container: element, // the dom element that will contain the animation
    //   renderer: 'svg',
    //   loop: true,
    //   autoplay: true,
    //   path: 'data.json' // the path to the animation json
    // })
  },
  mounted () {
    const params = {
      container: document.getElementById('circle'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: animationData
    }
    this.anim = this.lottie.loadAnimation(params)
  },
  methods: {
    dynamicProgress () {
      let timeInterval = null
      timeInterval = setInterval(() => {
        if (that.progressValue < 100) {
          that.progressValue += 2
        } else {
          clearInterval(timeInterval)
        }
      }, 200)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.page {
  width: 100%;
  height: 100%;
  padding-top: 40px;
  /* background: url(../assets/index/bg@2x.png) no-repeat; */
  background-size: 100% 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
</style>



